import React from "react";
import { Pane, Heading, Button, Paragraph, Link } from 'evergreen-ui';

import { InlineIcon } from '@iconify/react';

const ProjectLink = ({ url, icon, alt }) => (
    <Button
        appearance="minimal"
        is="a"
        href={url}
        margin={16}
        height={32}
    >
        <InlineIcon height={20} icon={icon} alt={alt} style={{textShadow: "3px  4px 2px #EDEDED"}}/>
    </Button>
)


const ProjectBadge = ({ img, link, alt }) => {
    const imageComponent = <img src={img} alt={alt} />;
    return link ? <Link margin={4} href={link}>{imageComponent}</Link> : imageComponent
}


const Project = ({ project }) => (
    <Pane clearfix>
        <Pane
            elevation={1}
            float="left"
            // background="tint2"
            style={project.style && project.style}
            height="15em"
            width="15em"
            margin={24}
            display="flex"
            justifyContent="center"
            alignItems="center"
            flexDirection="column"
        >
            <Paragraph>
                <Heading margin={16} size={600} style={{textShadow: "1px  2px 2px #EDEDED"}}>{project.title}</Heading>
            </Paragraph>
            <Paragraph>
                {project.badges.map(badge => <ProjectBadge key={badge.label} {...badge} />)}
            </Paragraph>
            <Paragraph>
                {project.links.map(link => <ProjectLink key={link.alt} {...link} />)}
            </Paragraph>
        </Pane>
    </Pane>
)

export default Project
